.page {
  min-height: 80vh;
  padding-top: 100px;
  padding-bottom: 4rem;
  position: relative;
}

.portfolioIntro {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 3rem;
  padding: 2rem;
  background: var(--glass-bg);
  backdrop-filter: var(--backdrop-blur);
  -webkit-backdrop-filter: var(--backdrop-blur);
  border: 1px solid var(--glass-border);
  box-shadow: var(--glass-shadow);
  border-radius: 20px;
}

.portfolioIntro p {
  line-height: 1.8;
  font-size: 1.05rem;
  opacity: 0.9;
  max-width: 70ch; /* Optimal line length */
  margin: 0 auto;
}

.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.searchWrapper {
  position: relative;
  width: 100%;
  max-width: 600px;
}

.searchInput {
  width: 100%;
  padding: 0.8rem 1rem 0.8rem 2.5rem;
  border: 2px solid var(--glass-border);
  border-radius: 16px;
  background: var(--glass-bg);
  backdrop-filter: var(--backdrop-blur);
  -webkit-backdrop-filter: var(--backdrop-blur);
  color: var(--text-color);
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: var(--glass-shadow);
}

.searchInput:focus {
  outline: none;
  background: var(--glass-bg-strong);
  border-color: transparent;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.2),
              0 8px 24px rgba(102, 126, 234, 0.15);
}

.searchInput::placeholder {
  color: var(--text-color);
  opacity: 0.5;
}

.searchIcon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.2rem;
  opacity: 0.5;
  pointer-events: none;
}


.portfolioGrid {
  width: 100%;
  max-width: 1400px; /* Constrain max width for better readability */
  margin: 0 auto; /* Center the grid */
  padding: 2rem 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Always 2 columns on desktop */
  gap: 1.5rem; /* Reduced gap */
  align-items: start;
}

/* Ensure items don't have extra margin */
.portfolioGrid > div {
  margin: 0;
}

/* Large desktop: Keep 2 columns with slightly more gap */
@media (min-width: 1400px) {
  .portfolioGrid {
    max-width: 1600px;
    gap: 2rem;
  }
}

/* Tablet: Keep 2 columns with reduced gap */
@media (max-width: 1200px) and (min-width: 768px) {
  .portfolioGrid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.25rem;
  }
}

/* Mobile: Single column */
@media (max-width: 767px) {
  .portfolioGrid {
    grid-template-columns: 1fr !important;
    gap: 1.5rem;
    padding: 1rem 0;
  }

  .portfolioGrid > div {
    width: 100%;
    max-width: 100%;
  }
}

/* Animation keyframes */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.noResults {
  text-align: center;
  padding: 4rem 2rem;
  background: var(--glass-bg);
  backdrop-filter: var(--backdrop-blur);
  -webkit-backdrop-filter: var(--backdrop-blur);
  border: 1px solid var(--glass-border);
  box-shadow: var(--glass-shadow);
  border-radius: 20px;
  margin: 2rem 0;
}

.noResults p {
  font-size: 1.2rem;
  opacity: 0.7;
}

@media (max-width: 768px) {
  .page {
    padding-top: 80px;
    padding-bottom: 2rem;
  }

  .portfolioIntro {
    padding: 1.5rem;
    margin-bottom: 2rem;
  }

  .portfolioIntro p {
    font-size: 1rem;
  }

  .controls {
    justify-content: center;
    margin-bottom: 1.5rem;
  }

  .searchWrapper {
    max-width: 100%;
  }
  
  .portfolioGrid {
    padding: 1rem 0;
    gap: 1.5rem;
  }
}
